import Vue from "vue";
import Vuex from "vuex";
import * as api from "@/api/index"
import { Toast } from 'vant';
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user:localStorage.user?JSON.parse(localStorage.user):{}
  },
  getters: {},
  mutations: {
    setUsers(state,payload){
      state.user = payload
    }
  },
  actions: {
    async login(content,payload){
      // 将表单提交的账号密码进行提交payload={username,password}
      // 当登录成功，后端返回用户信息，进行修改用户信息
      // 保存登录的凭证token
      // token:通过这个凭证就可以进行访问
      // access-token就是登陆凭证，他会有有效期，比如1小时有效
      // refresh-token：当access-token失效的时候，使用refresh-token进行获取access-token，就可以用新的token进行登陆
      // 若refresh-token都失效了，就必须重新登录
      let result = await api.postLogin(payload)
      if(result.msg==="操作成功"){
        // Toast.fail(result.result)
        Toast.success('登录成功')
        content.commit('setUsers',result.result)
        localStorage.user=JSON.stringify(result.result)
      }else{
        Toast({
          message:result.result,
          icon:"/imgs/fail.png",
        })
      }
      console.log(result)
      return result
    }
  },
  modules: {},
});
