<template>
    <div class="lc-list">
        <slot></slot>
        <div class="loading" v-show="value">加载中</div>
        <div class="finish" v-if="finished">{{finishedText}}</div>
    </div>
</template>

<script >
export default{
    name:"lc-list",
    props:['finished-text','finished','loading',"value"],
    methods:{
        emitLoad(){
            this.$emit('load')
        },
        debounce(fn,delay){
            let timer = null;
            return function(){
                if(timer){
                    clearTimeout(timer)
                    timer = setTimeout(fn, delay);
                }else{
                    timer = setTimeout(fn, delay);
                }
            }
        }
    },


    async mounted(){
        this.emitLoad()
        console.log(this)
        // 只有当滚动停下来0.25s才会触发
        let timer = null;
        window.addEventListener('scroll',this.debounce(() =>{
            let scrollHeight = document.documentElement.scrollHeight
            let scrollTop = document.documentElement.scrollTop
            let innerHeight = window.innerHeight
            console.log(scrollTop)
            if(scrollHeight-scrollTop-innerHeight<50){
                this.emitLoad()
            }
        }, 250))
            
            
        
    },
}
</script>

<style scoped>
.finish,.loading{
    color:#bbb;
    background-color: #efefef;
}
</style>