<template>
    <div class="articles">
        <!-- 
            v-model等同于绑定了value值属性和监听了input的事件
            :value="loading"
            @input="(value)=>(loading=value)"


            v-bind绑定替换成v-model绑定
            :loading="loading"
         -->
        <lc-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="getList"
        >
            <van-cell v-for="item,i in list" :key="i" :to="{name:'article',params:{id:item.id,article:item}}">
                <div class="cell-header">
                    <van-image
                        round
                        width="30"
                        height="30"
                        :src="item.original_status.user.photo_domain+item.original_status.user.profile_image_url.split(',')[0]"
                    ></van-image>
                    <div class="right-header">
                        <div class="author">{{item.original_status.user.screen_name}}</div>
                        <div class="pubtime">{{item.original_status.timeBefore}}</div>
                    </div>
                </div>
                <p v-html="item.original_status.description"></p>
                <div class="cell-footer">
                    <div class="item">
                        <van-icon name="share-o"></van-icon>
                        <div class="num">{{item.original_status.retweet_count}}</div>
                    </div>
                    <div class="item">
                        <van-icon name="chat-o"></van-icon>
                        <div class="num">{{item.original_status.reply_count}}</div>
                    </div>
                    <div class="item">
                        <van-icon name="good-job-o"></van-icon>
                        <div class="num">{{item.original_status.like_count}}</div>
                    </div>
                </div>
            </van-cell>
        </lc-list>
    </div>
</template>

<script>
import * as api from "@/api/index"
import LcList from "@/components/Lc-list"
export default{
    data(){
        return{
            page:1,
            list:[],
            finished:false,
            loading:false,
        }
    },
    components:{
        LcList
    },
    methods:{
        async getList(){
            if(this.finished){
                return
            }
            this.loading = true
            let result = await api.getArticles({page:this.page});
            console.log(result)
            this.loading = false
            if(result.result && result.result.indexOf('已加载')!==-1){
                // console.log(result.result)
                this.finished = true
            }else{
                this.list = this.list.concat(result.items)
                this.page++
            }
        },
   
    },
    
    

}   
</script>

<style scoped lang="less">
.cell-header{
    display: flex;
    height: 1rem;
    .right-header{
        font-size: 0.3rem;
        line-height: 1rem;
        padding-left: 1rem;
        .pubtime{
            color:#999
        }
    }
}   
p{
    padding:1rem 0 ;
    margin-bottom: 0;
}
.cell-footer{
    display: flex;
    height: 0.5rem;
    align-items: center;
    .item{
        display: flex;
        font-size: 0.3rem;
        width:3.5rem;
        align-items: center;
        .num{
            padding: 0 0.1rem;
        }
    }
}

.finish{
    color:#bbb;
    background-color: #efefef;
}
.loading{
    color:#bbb;
    background-color: #efefef;
}
</style>